import React, { Component } from 'react'
import { NavLink } from 'react-router-dom'
import '../Styles/navigation.css'

class Navbar extends Component {

    state = {
        hidden : true
    }
    
    
    handlehidden = (e) =>{
        console.log(this.state)
        this.setState({
            hidden : false
        })
        
        
    }

    render(){
        return(
        
            <div className="navigation">
                <ul>
                    <NavLink to="/" exact className="hover"
                    activeClassName="nav-active">
                        <li>accueil</li>
                    </NavLink>
                    
                        <li className="hover" activeClassName="nav-active" onClick={this.handlehidden} >Projets
                            <ul hidden={this.state.hidden}>
                                <NavLink to="/P_Folios" className="hover"
                                    activeClassName="nav-active">
                                    <li>Portfolios</li>
                                </NavLink>
                                <NavLink to="/exemple2" className="hover"
                                    activeClassName="nav-active">
                                    <li>site exemple2</li>
                                    </NavLink>
                                
                            </ul>
                        </li>
                    
                    <NavLink to="/contact" exact className="hover"
                    activeClassName="nav-active">
                        <li>contact</li>
                    </NavLink>
                    <NavLink to="/knowledges" exact className="hover"
                    activeClassName="nav-active">
                        <li>CV</li>
                    </NavLink>
                </ul>   
            </div>
        );
    }


    
} 

export default Navbar
