import React, { Component } from 'react'
import './Styles/NavbarPerso.css'




class NavbarPerso extends Component{ 

    state = {
        hidden : true
    }
        
        
    handlehidden = (e) =>{
        console.log(this.state)
        if (this.state.hidden == true){
            this.setState({
                hidden : false
            })
        
        }else{
            this.setState({
                hidden : true
            })
        
        }
        
    }
    render() {
        return(
            <div>
            <label for="toggle-1"  >
                    <ul className="points" onClick={this.handlehidden}>
                        <li></li>
                        <li></li>
                        <li></li>
                    </ul>
                </label>
                 
                <nav>
                    <ul className="ssmenu" hidden={this.state.hidden}>
                        <li><a href="/"><i class="fa fa-home"></i> Home</a></li>
                        <li><a href="#Nos Services">Nos Services</a></li>
                        <li><a href="#Nos abonnements">Nos abonnements</a></li>
                        <li><a href="#Nos partenaires">Nos partenaires</a></li>
                        <li><a href="#LiensOutils">Liens et Outils</a></li>
                        <li><a href="#contact">Contact</a></li>
                    </ul>
                </nav>
        </div>
        )
    }

}


export default NavbarPerso 


