import React, { Component } from 'react'
import form from '../images/fotolia_35584964_xs.png'
import actu from '../images/promoconso_1.png'
import maintenance from '../images/packconfort.png'
import bouee from '../images/bouee.png'

class Contenu extends Component{

    addStyle = (e) =>{
        // si on pointe sur le titre et que le style est déja appliqué
        if(e.target.classList.contains('addStyle')){
            // alors on retire le style
            e.target.classList.remove('addStyle')
            // sinon on ajoute le style
        } else {
            e.target.classList.add('addStyle')
        }
            
    }

    render(){
        return(
        <div className="contenuPri">
            <div className='formules'>
                <h3 onMouseOver={this.addStyle}>Nos formules</h3>
                <img className="imageFormule" src={form}></img>
                <p className="txt">Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolor eaque quos et modi aliquam ullam laudantium dolorem. Laborum, quo magnam natus nisi error, culpa dolorum libero ut, a quas ullam.</p>
                <button className="btnContact"><a href="#" >Contactez-nous</a></button>
            </div>
            <div>
            <div className="consult">
                <h3 onMouseOver={this.addStyle}>Votre consultant en informatique</h3>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cupiditate quod, quis distinctio nesciunt praesentium dolores consequatur reiciendis natus, optio a sequi voluptas odio. Eligendi ab nostrum unde natus voluptas est.</p>
            </div>
            <div className="confier">
                <h3 onMouseOver={this.addStyle}>Confiez votre informatique à des professionnels !</h3>
                <img className="maintenance" src={maintenance}></img>
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Cupiditate quod, quis distinctio nesciunt praesentium dolores consequatur reiciendis natus, optio a sequi voluptas odio. Eligendi ab nostrum unde natus voluptas est.</p>
            </div>
            </div>
                <div>
                    <div className='actualites'>
                        <h3 onMouseOver={this.addStyle}>Actualités</h3>
                        <img className="imagePromo" src={actu}></img>
                        
                    </div>
                    <div className='imagebouee'>
                    <h3 onMouseOver={this.addStyle}>Dépannage</h3>
                <img  className="imagebouee" src={bouee}></img>
                <p>Cliquez sur la bouée pour obtenir un dépannage à distance</p>
                        
                    </div>
                </div>    
            
        </div>
        )
    }
}
export default Contenu